.arrow-btn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  font-size: 0;
  border: 2px solid #FF3E79;
  border-radius: 50%; }
  .arrow-btn::before {
    content: '';
    display: block;
    margin-left: -4px;
    width: 8px;
    height: 8px;
    border: 2px solid #FF3E79;
    border-left: transparent;
    border-bottom: transparent;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg); }

.order-btn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  max-width: 136px;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 12px;
  font-weight: 900;
  line-height: 24px;
  letter-spacing: -0.15px;
  text-transform: uppercase;
