    width: 8px;
    height: 8px;
    margin-left: 10px;
    border: 2px solid #fff;
    border-left: transparent;
    border-bottom: transparent;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg); }

