*, *::before, *::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

*:focus {
  outline: none;
  -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2); }

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  font-family: "TT Lakes";
  color: #000; }

img {
  max-width: 100%; }

.headline-hidden {
  font-size: 0; }
